import { GetComponentProps, useEditor, useEditorActions, Modal } from 'topwrite';
import { ReactComponent as Icon } from '../images/icon.svg';

export default function Tool({ Component }: GetComponentProps<'editor:tool'>) {

    const editor = useEditor();

    const handler = async () => {
        const { insertXBlock } = useEditorActions();

        const result = await Modal.prompt<string>({
            title: '插入JSFiddle',
            schema: {
                type: 'string',
                title: 'JSFiddle地址'
            }
        });
        if (result) {
            insertXBlock(editor, {
                name: 'jsfiddle',
                value: result
            });
        }
    };

    return <Component
        title={'JSFiddle'}
        handler={handler}
    >
        <Icon />
    </Component>;
}
